import React, { CSSProperties, MouseEventHandler, forwardRef, useCallback, useEffect, useImperativeHandle, useMemo, useRef, useState } from "react";
import { Canvas, ThreeEvent, useThree } from "@react-three/fiber";
import useMeshFloors, { textLogoNamePrefix } from "../../Hooks/useMeshFloors";
import { SceneProperties } from "./SceneProperties";
import { CameraProperties } from "./CameraProperties";
import { DoubleSide, Mesh, MeshLambertMaterial, Object3D } from "three";
import { IAmenitiesInteractiveList, IExtMesh, IJsonConfig, IMeshParams, IMeshValues, TMapMode, TMapSettingsProps } from "src/types";
import { FloorsMap } from "./FloorsMap";
import { useMapit2Data } from "../../useMapit2Data";
import { MapIt2Response, MapObj, MapObjToSave } from "../../mapitApiTypes";
import { Stats } from '@react-three/drei';
import UIComponent from "../../components/UIComponent";
import { MapCenterMarker } from "./MapCenterMarker";
import { delete_route_path } from "src/helpers/route.helpers";
import { useMeshObjectContext } from "src/contexts/MeshObjectContextProvider";
import MapboxForm from "../../components/MapboxForm/MapboxForm";
import styles from '../../MapBox.module.css';
import { Alert } from 'react-bootstrap';
import { hex_to_color } from "src/helpers/misc";
import RetailerInfoModal from "./RetailerInfoModal";
import { Vector3 } from "three";
import { amenitiesList } from "src/helpers/amenities.helper";
import { AmenityID } from "src/MapBox";

interface ISceneComponentProps {
    mapitData?: MapIt2Response;
    config: Partial<IJsonConfig>;
    selectedActiveObjectId: string;
    setSelectedActiveObjectId: React.Dispatch<React.SetStateAction<string>>;
    onMapDataUpdate?: (data: MapObj[]) => void;
    onSettingsLoading?: (settings: MapIt2Response) => void;
    webApiURI?: string;
    mediaStorageURI?: string;
    onSubmit?: (data: MapObjToSave, refreshData?: () => void) => void;
    onResetData?: (data: MapObj) => void;
    deviceType?: string | "";
    setCameraControlPosition?: (cameraControlPos: any) => void;
    onExtractedAmenities?: (amenityIds: AmenityID[]) => void;
}

export interface IZoomData {
    direction: 'in' | 'out';
}

export type TFormMapObjData = {
    index: number,
    data: MapObj,
}

const SceneComponent = forwardRef((params: ISceneComponentProps, ref) => {
    const { data, refreshData } = useMapit2Data({ mapitData: params.mapitData, CENTER_ID: params.config.CENTER_ID as string, webApiURI: params.webApiURI });
    const [fromObjectId, setFromObjectId] = useState<string>('');
    const [hovered, setHovered] = useState<boolean>(false);
    const [selectedFloorIndex, setSelectedFloorIndex] = useState<number>(-1);
    const [formMapObjData, setFormMapObjData] = useState<TFormMapObjData[]>([]);
    const [cameraControlState, setCameraControlState] = useState<any>(null);
    const [cameraPos, setCameraPos] = useState<{ x: number, y: number, z: number } | null>();
    const [controlPos, setControlPos] = useState<{ x: number, y: number, z: number } | null>();
    const [canvasWidth, setCanvasWidth] = useState<number | undefined>(0);
    const { selectedActiveObjectId, setSelectedActiveObjectId, onMapDataUpdate, deviceType, setCameraControlPosition } = params;

    const [amenityTargetType, setAmenityTargetType] = useState<string>('');
    const [selectedAmenityType, setSelectedAmenityType] = useState<string>('');
    const [zoom, setZoom] = useState<IZoomData | null>(null);

    const [cameraLength, setCameraLength] = useState<number>(0);

    const statsParentRef = useRef<HTMLDivElement | null>(null);
    const canvasWidthRef = useRef<number | undefined | null>(null);
    canvasWidthRef.current = canvasWidth;
    const cameraControlStateRef = useRef<any>(null);
    cameraControlStateRef.current = cameraControlState;
    const deviceTypeRef = useRef<string | undefined | null>(null);
    deviceTypeRef.current = deviceType;
    const floorsMapRef = useRef<{ resetCamera: (fullReset?: boolean, cameraControlsStates?: any) => void | null }>(null);

    const handleCameraLength = useCallback((length: number) => {
        setCameraLength(length);
    }, [])

    const handleFloorChange = (floorIndex: number): MouseEventHandler<HTMLDivElement> => (e) => {
        setSelectedFloorIndex(floorIndex);
    }

    const meshFloors = useMeshFloors(data, params.config, params.config.ROLE, params.mediaStorageURI);
    const mapCenterMarkerRef = useRef(null);
    const labelRef = useRef<HTMLDivElement>(null);
    const [currentHoveredObject, setCurrentHoveredObject] = useState<Object3D | null>(null);
    const meshObjectContext = useMeshObjectContext();

    const [showRetailerInfo, setShowRetailerInfo] = useState<boolean>(false);
    const config = meshFloors.config;
    const floors = meshFloors.floors;
    const meshParams = meshFloors.meshParams;

    useImperativeHandle(ref, () => ({
        refreshData,
        createRouteToAmenity: handleAmenityClick,
    }))

    useEffect(() => {
        if (statsParentRef.current) {
            const resizeWidth = () => {
                setCanvasWidth(statsParentRef?.current?.offsetWidth);
            }
            window.addEventListener('resize', () => {
                resizeWidth();
                // Update your map or camera settings based on newWidth
            });
            resizeWidth();
        }
    }, [statsParentRef.current]);

    useEffect(() => {
        if (data && data.camera_controls_states) {
            setCameraControlState(data.camera_controls_states);
            setCameraControlPos(data.camera_controls_states);
        }
    }, [data]);

    useEffect(() => {
        if (cameraControlStateRef.current) {
            setCameraControlPos(cameraControlStateRef.current);
        }
    }, [deviceType, canvasWidth])

    const setCameraControlPos = (camera_controls_states: any) => {
        if (config?.ROLE === "DISPLAY_APP") {
            setCameraPos(camera_controls_states.display_app.camera)
            setControlPos(camera_controls_states.display_app.controls)
        } else if (config?.ROLE === "WP_SITE" && canvasWidth) {
            if (canvasWidth > 1024) {
                setCameraPos(camera_controls_states.desktop.camera)
                setControlPos(camera_controls_states.desktop.controls)
            } else if (canvasWidth > 600) {
                setCameraPos(camera_controls_states.tablet.camera)
                setControlPos(camera_controls_states.tablet.controls)
            } else {
                setCameraPos(camera_controls_states.mobile.camera)
                setControlPos(camera_controls_states.mobile.controls)
            }
        } else if (config?.ROLE === "PORTAL_RESPONSIVE") {
            if (deviceTypeRef.current === "desktop") {
                setCameraPos(camera_controls_states.desktop.camera)
                setControlPos(camera_controls_states.desktop.controls)
            } else if (deviceTypeRef.current === "display_app") {
                setCameraPos(camera_controls_states.display_app.camera)
                setControlPos(camera_controls_states.display_app.controls)
            } else if (deviceTypeRef.current === "mobile") {
                setCameraPos(camera_controls_states.mobile.camera)
                setControlPos(camera_controls_states.mobile.controls)
            } else if (deviceTypeRef.current === "tablet") {
                setCameraPos(camera_controls_states.tablet.camera)
                setControlPos(camera_controls_states.tablet.controls)
            }
        }
    }

    useEffect(() => {
        if (data && meshFloors.config && params.onSettingsLoading) {
            params.onSettingsLoading(
                {
                    ...data,
                    settings: {
                        ...data.settings,
                        STYLE: meshFloors.config.STYLE,
                        ACCENT_COLOR: meshFloors.config.ACCENT_COLOR.getHexString(),
                        AMENITIES_NAV_BG_COLOR: meshFloors.config.AMENITIES_NAV_BG_COLOR.getHexString(),
                        AMENITIES_NAV_ICON_COLOR: meshFloors.config.AMENITIES_NAV_ICON_COLOR.getHexString(),
                        BASE_COLOR: meshFloors.config.BASE_COLOR.getHexString(),
                        BIG_STORE_DEFAULT_COLOR: meshFloors.config.BIG_STORE_DEFAULT_COLOR.getHexString(),
                        BOUNDARY_COLOR: meshFloors.config.BOUNDARY_COLOR.getHexString(),
                        BOUNDARY_THICKNESS: meshFloors.config.BOUNDARY_THICKNESS.toString(),
                        MAP_BACKGROUND_COLOR: meshFloors.config.MAP_BACKGROUND_COLOR.getHexString(),
                        OVERLAY_COLOR: meshFloors.config.OVERLAY_COLOR.getHexString(),
                        OVERLAY_OPACITY: meshFloors.config.OVERLAY_OPACITY.toString(),
                        STORE_DEFAULT_COLOR: meshFloors.config.STORE_DEFAULT_COLOR.getHexString(),
                        STORE_TEXT_COLOR: meshFloors.config.STORE_TEXT_COLOR.getHexString(),
                        WALL_COLOR: meshFloors.config.WALL_COLOR.getHexString(),
                        WALL_THICKNESS: meshFloors.config.WALL_THICKNESS.toString(),
                        KIOSK_SIZE: meshFloors.config.KIOSK_SIZE ? meshFloors.config.KIOSK_SIZE : '15',
                        STORE_HEIGHT: meshFloors.config.STORE_HEIGHT || '8',
                        CAMERA_MAX_DISTANCE: meshFloors.config.CAMERA.maxDistance.toString(),
                        CAMERA_MIN_DISTANCE: meshFloors.config.CAMERA.minDistance.toString(),
                    }
                });
        }
    }, [data, meshFloors])

    const handleCameraAndControlsChange = (camPos: { x: number, y: number, z: number }, ctrlTarget: { x: number, y: number, z: number }) => {
        if(canvasWidthRef.current === 0) return;
        if (config?.ROLE === "DISPLAY_APP") {
            setCameraControlState({ ...cameraControlStateRef.current, "display_app": { camera: camPos, controls: ctrlTarget } });
        } else if (config?.ROLE === "WP_SITE" && canvasWidthRef.current) {
            if (canvasWidthRef.current > 1024) {
                setCameraControlState({ ...cameraControlStateRef.current, "desktop": { camera: camPos, controls: ctrlTarget } });
            } else if (canvasWidthRef.current > 600) {
                setCameraControlState({ ...cameraControlStateRef.current, "tablet": { camera: camPos, controls: ctrlTarget } });
            } else {
                setCameraControlState({ ...cameraControlStateRef.current, "mobile": { camera: camPos, controls: ctrlTarget } });
            }
        } else if (config?.ROLE === "PORTAL_RESPONSIVE") {
            if (deviceTypeRef.current === "desktop") {
                setCameraControlState({ ...cameraControlStateRef.current, "desktop": { camera: camPos, controls: ctrlTarget } });
            } else if (deviceTypeRef.current === "display_app") {
                setCameraControlState({ ...cameraControlStateRef.current, "display_app": { camera: camPos, controls: ctrlTarget } });
            } else if (deviceTypeRef.current === "mobile") {
                setCameraControlState({ ...cameraControlStateRef.current, "mobile": { camera: camPos, controls: ctrlTarget } });
            } else if (deviceTypeRef.current === "tablet") {
                setCameraControlState({ ...cameraControlStateRef.current, "tablet": { camera: camPos, controls: ctrlTarget } });
            }
        }
    }

    useEffect(() => {
        if (cameraControlStateRef.current && setCameraControlPosition) {
            setCameraControlPosition(cameraControlStateRef.current);
        }
    }, [cameraControlState])

    // if selectedActiveObjectId is not -1, then it is set to the DEFAULT_SELECTED_STORE
    let activeFromObjectId = '';
    let activeObjectId = config?.DEFAULT_SELECTED_STORE ?? '';
    if (selectedActiveObjectId) {
        activeObjectId = selectedActiveObjectId;
        activeFromObjectId = fromObjectId;
    } else if (amenityTargetType) {
        activeObjectId = '';
        activeFromObjectId = fromObjectId;
        // activeFromObjectId = '';
    }

    const handleChangeMapitData = (index: number, newData: MapObj) => {
        const itemIndex = formMapObjData.findIndex((item) => item.data.map_obj_name === newData.map_obj_name);

        if (itemIndex !== -1) {
            formMapObjData[itemIndex] = { index, data: { ...newData } };
        } else {
            formMapObjData.push({ index, data: { ...newData } })
        }

        setFormMapObjData([...formMapObjData]);
    }

    const getMapitData = (): MapIt2Response | null => {
        if (data && data.map_objs) {
            if (formMapObjData) {
                formMapObjData.forEach((value) => {
                    const index = data.map_objs.findIndex((item: MapObj) => item.map_obj_name === value.data.map_obj_name);
                    if (index !== -1) {
                        data.map_objs[index] = { ...value.data };
                    }
                })
            }
        }

        return data;
    }

    const handleOnSubmit = (data: MapObjToSave) => {
        if (params.onSubmit) {
            params.onSubmit(data, refreshData);
        }
    }

    useEffect(() => {
        document.body.style.cursor = currentHoveredObject ? 'pointer' : 'default';
    }, [currentHoveredObject]);

    const [currKioskObj, currKioskFloorIndex, kioskError]: [Mesh | null, number, string] = useMemo(() => {
        if (!config) {
            return [null, 0, ''] // not an error - loading/initializing
        }
        let kioskError = '';
        let currKioskObj: Mesh | null = null;
        let currKioskFloorIndex = 0;
        if (config.KIOSKS && config.KIOSKS[config.KIOSK]) {
            const kioskMapObjName = config.KIOSKS[config.KIOSK].map_obj_name;
            const currKioskMeshParams = meshParams.flat().find(mesh => mesh.object_id === kioskMapObjName);
            if (currKioskMeshParams) {
                currKioskObj = currKioskMeshParams.mesh;
                currKioskFloorIndex = currKioskMeshParams.floor_index;
            }
        } else {
            if (config.ROLE === 'WEBSITE') {
                // kioskError = "This Kiosk is not assigned in MAP, so routes won't work. Please assign the Kiosk from Edit Map first.";
            }
            if (config.ROLE === 'WEBSITE') {
                console.error('Kiosk not found in config');
                // kioskError = 'Map initialization error';
            }
        }

        return [currKioskObj, currKioskFloorIndex, kioskError]
    }, [config, meshParams]);

    const resetHandle = () => {

        setSelectedFloorIndex(currKioskFloorIndex);
        setSelectedActiveObjectId('');
        setFromObjectId('');
        setAmenityTargetType('');

            if (floorsMapRef.current) {
                if (config?.ROLE === "PORTAL_RESPONSIVE" && data && data.camera_controls_states) {
                    let ccs = null;
                    if (deviceTypeRef.current === "desktop") {
                        ccs = { ...data.camera_controls_states.desktop }
                    } else if (deviceTypeRef.current === "display_app") {
                        ccs = { ...data.camera_controls_states.display_app }
                    } else if (deviceTypeRef.current === "mobile") {
                        ccs = { ...data.camera_controls_states.mobile }
                    } else if (deviceTypeRef.current === "tablet") {
                        ccs = { ...data.camera_controls_states.tablet }
                    }
                    console.debug({
                        device: deviceTypeRef.current,
                        ccs
                    })
                    if (ccs) {
                        floorsMapRef?.current?.resetCamera(false, ccs);
                    }
                } else {

                    floorsMapRef?.current?.resetCamera();

                }
            }

    }

    useEffect(() => {
        if (!config) {
            return;
        }

        const kiosks: IMeshValues[] = [];
        meshFloors.meshParams.forEach(floor => {
            floor.forEach(item => {
                if (item?.mesh_type === "kiosk") {
                    kiosks.push(item);
                }
            })
        })
        // console.log('kiosks', kiosks);

        const mapObjectIdToKiosk = new Map();
        const mapObjectIdToLogo = new Map();
        kiosks.forEach(item => {
            mapObjectIdToKiosk.set(item.object_id, item.mesh);
        });
        // console.log('meshFloors.storeLogos', meshFloors.storeLogos)
        meshFloors.storeLogos.filter(item => item !== undefined).forEach(floor => {
            floor.forEach(item => {
                mapObjectIdToLogo.set(item.storeLogo.object_id, item.storeLogo);
            })
        });
        // console.log('mapObjectIdToKiosk', mapObjectIdToKiosk);
        // console.log('mapObjectIdToLogo', mapObjectIdToLogo);

        const currKioskId = (currKioskObj as IExtMesh)?.object_id;

        const koef = cameraLength / (config.CAMERA.maxDistance - config.CAMERA.minDistance);
        const currKioskLogo: IExtMesh[] = [];
        const mapData = getMapitData();
        kiosks.forEach(item => {
            const kiosk = item.mesh;
            const logo = mapObjectIdToLogo.get('custom-layer-' + item.object_id);
            const mapObj = mapData?.map_objs.find(item => item.map_obj_name === kiosk.object_id);
            if (kiosk && logo) {
                //TODO use 'value' until api with lock_size will be working
                const newKoef = mapObj?.value === '1' ? 1 : 1 - koef;
                logo.userData.htmlContent = <MapCenterMarker blink={item.object_id === currKioskId} size={mapObj ? Number(mapObj.size) : Number(config.KIOSK_SIZE)} koef={newKoef} />
                // logo.userData.distanceFactor = mapObj?.value === '1' ? 1500 : undefined;
                logo.visible = false;

                currKioskLogo.push(logo);
            }
        });

        return () => {
            currKioskLogo?.forEach(mesh => {
                mesh.userData.htmlContent = null;
            });
        }

    }, [meshFloors, currKioskObj, cameraLength, config]);

    useEffect(() => {
        if (meshFloors.meshParams && meshFloors.meshParams.length && selectedActiveObjectId && meshObjectContext?.SetMeshObject) {
            const object = meshFloors.meshParams[selectedFloorIndex === -1 ? 0 : selectedFloorIndex].find((value) => value.object_id === selectedActiveObjectId);
            if (object) {
                //@ts-ignore
                handleMeshObjectContext(object);
            }
            //@ts-ignore
        }
    }, [meshFloors])

    useEffect(() => {
        if (onMapDataUpdate) {
            const exportData: MapObj[] = [];

            formMapObjData.forEach((value) => {
                exportData.push({ ...value.data });
            })
        }
    }, [formMapObjData])

    useEffect(() => {
        document.body.style.cursor = hovered ? 'pointer' : 'auto';

    }, [hovered])

    useEffect(() => {
        if (config && data && data.retailers && data.map_objs && config.SELECTED_RETAILER_SLUG) {
            const retailer = data.retailers.find(item => {
                return item.slug === config.SELECTED_RETAILER_SLUG;
            })
            if (retailer) {
                const mapObject = data.map_objs.find(item => item.retailer_id === retailer.id);
                if (mapObject) {
                    setSelectedActiveObjectId(mapObject.map_obj_name);
                }
            }
        }
    }, [config, data, setSelectedActiveObjectId])


    const handleMeshObjectContext = useCallback((object: IExtMesh) => {
        if (meshObjectContext?.SetMeshObject) {
            const textParams = meshFloors.textParams[selectedFloorIndex === -1 ? 0 : selectedFloorIndex]?.find(value => {
                return value.textMesh.object_id === `${textLogoNamePrefix}${(object as IExtMesh).object_id}`
            })

            const logoParams = meshFloors.storeLogos[selectedFloorIndex === -1 ? 0 : selectedFloorIndex]?.find(value => {
                return value.storeLogo.object_id === `${textLogoNamePrefix}${(object as IExtMesh).object_id}`
            })

            const result = [object as IExtMesh];

            if (textParams) {
                result.push(textParams.textMesh);
            }

            if (logoParams) {
                result.push(logoParams.storeLogo)
            }

            meshObjectContext.SetMeshObject([...result]);
        }
    }, [meshFloors.storeLogos, meshFloors.textParams, meshObjectContext, selectedFloorIndex])

    const handleAmenityClick = useCallback((type: string) => {
        setSelectedActiveObjectId('')
        setFromObjectId('')
        if (config && config.ROLE !== 'PORTAL' && !config.KIOSK) {
            setSelectedAmenityType(type)
            setShowRetailerInfo(true);
        } else {
            setAmenityTargetType(type)
        }
    }, [config, setSelectedActiveObjectId]);

    const onClick = useCallback((e: ThreeEvent<MouseEvent>) => {
        e.stopPropagation();
        // console.log('onClick', e.object)

        const targetId = (e.object as IExtMesh).object_id;
        const mapObj = data?.map_objs.find(item => item.map_obj_name === targetId);
        if (mapObj && mapObj.layer_type === 'amenity') {
            handleAmenityClick(mapObj.value);
            if (config && config.ROLE === 'PORTAL') {
                handleMeshObjectContext(e.object as IExtMesh);
                setSelectedActiveObjectId(targetId ?? '');
            }
        } else {
            handleMeshObjectContext(e.object as IExtMesh);
            setSelectedActiveObjectId(targetId ?? '');
            // console.log('set active object', targetId);
            setAmenityTargetType('');
            setFromObjectId('');
            if (config && config.ROLE !== 'PORTAL' && !config.KIOSK) {
                setShowRetailerInfo(true);
            }
        }
    }, [config, handleAmenityClick, setSelectedActiveObjectId, data?.map_objs, handleMeshObjectContext]);

    const onCameraMove = useCallback((e: any) => {
        if (!mapCenterMarkerRef || !mapCenterMarkerRef.current) {
            return
        }
    }, []);

    const onPointerEnter = useCallback((e: ThreeEvent<PointerEvent>) => {
        e.stopPropagation();
        console.debug()
        setHovered(true);
        // console.log('onPointerEnter', e.object)
        if (!e.object.userData.storeName && config && config.ROLE !== 'PORTAL') {
            // console.warn('onPointerEnter no storeName', e.object);
            return;
        }
        setCurrentHoveredObject(e.object);
    }, []);

    const onPointerLeave = useCallback((e: ThreeEvent<PointerEvent>) => {
        e.stopPropagation();
        // console.log('onPointerLeave', e.object)
        setCurrentHoveredObject(null);
        setHovered(false);
        if (labelRef && labelRef.current) {
            labelRef.current.style.display = 'none';
        }
    }, []);

    const onPointerMove = (e: ThreeEvent<PointerEvent>) => {
        e.stopPropagation();
        // console.log('onPointerMove', e.object)

        if (labelRef && labelRef.current && e.object.userData.storeName && e.object.userData.storeName !== '') {
            labelRef.current.style.top = `${e.offsetY - 65}px`;
            labelRef.current.style.left = `${e.offsetX}px`;
            labelRef.current.style.display = 'block';
        } else if (labelRef.current) {
            labelRef.current.style.display = 'none';
        }
    }

    const messageStyle: CSSProperties = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%',
    }

    if (data && (!data.floors || data.floors.length <= 0)) {
        return <div style={messageStyle}>Map doesn't have any floors</div>
    }

    if (!config || !meshFloors.meshParams.length) {
        console.warn('LOADING MAP')
        if (config?.ROLE === 'WP_SITE') {
            return <div style={messageStyle}>No map for Center</div>
        }
        return <div style={messageStyle}>Map loading, please wait...</div>
    }

    if (kioskError) {
        return <div style={messageStyle}>{kioskError}</div>
    }

    const currentFloorIndex = selectedFloorIndex > -1 ? selectedFloorIndex : currKioskFloorIndex;

    if (!currKioskObj && config.ROLE === 'WEBSITE') {
        console.error('NO KIOSK OBJECT FOUND');
        // return <div style={messageStyle}>Map initialization error</div>
    }

    // make sure that meshFloors conditionals are not null
    const meshFloorsChecked: IMeshParams = meshFloors as IMeshParams;


    let hoverObject = currentHoveredObject as IExtMesh
    let hoverObjectId = '';
    if (hoverObject && hoverObject.object_id) {
        hoverObjectId = hoverObject.object_id;
    }

    const handleRetailerInfoOnClick = (retailerId: string, amenityType?: string) => {
        if (!amenityType) {
            setFromObjectId(retailerId);
        } else {
            // setSelectedActiveObjectId(retailerId);
            setFromObjectId(retailerId);
            setAmenityTargetType(amenityType);
        }
        setShowRetailerInfo(false);
    }

    return (
        <>
            {config.ROLE === 'PORTAL' &&
                <div className={styles['mapbox-admin-form']}>
                    {!selectedActiveObjectId ?
                        <Alert variant="info">Select a store to customize.</Alert>
                        :
                        <MapboxForm
                            floorIndex={currentFloorIndex}
                            meshFloors={meshFloors}
                            config={config}
                            data={getMapitData()}
                            setData={handleChangeMapitData}
                            selectedId={selectedActiveObjectId}
                            centerId={params.config.CENTER_ID as string}
                            onSubmit={handleOnSubmit}
                            apiURI={params.webApiURI}
                            onResetData={params.onResetData}
                        />
                    }
                </div>
            }

            <div ref={statsParentRef} style={{ position: 'relative', width: '100%' }}>
                <UIComponent
                    accentColor={config.ACCENT_COLOR.getStyle()}
                    floors={floors}
                    selectedFloorIndex={currentFloorIndex}
                    handleFloorChange={handleFloorChange}
                    amenitiesList={amenitiesList}
                    handleAmenityClick={handleAmenityClick}
                    reset={resetHandle}
                    zoomIn={() => setZoom({ direction: 'in' })}
                    zoomOut={() => setZoom({ direction: 'out' })}
                    role={config.ROLE}
                    mapObjs={data?.map_objs}
                    onExtractedAmenities={params.onExtractedAmenities}
                />
                <Canvas style={{ position: 'absolute', top: 0, left: 0 }} flat>
                    {Number(params.config.STATS) ? <Stats className={styles.stats} parent={statsParentRef} /> : null}
                    <SceneProperties background={config.MAP_BACKGROUND_COLOR} />
                    <CameraProperties near={1} far={config.CAMERA.far} fov={config.CAMERA.fov} />
                    {/*<axesHelper args={[5000]} />*/}

                    <ambientLight intensity={1} color={'#c4c4c4'} />
                    <directionalLight position={[0, 400, 0]} color={'#ffffff'} intensity={0.3} />
                    <directionalLight position={[0, 0, 400]} color={'#c4c4c4'} intensity={0.2} />
                    <directionalLight position={[0, 0, -400]} color={'#c4c4c4'} intensity={0.2} />
                    <FloorsMap
                        onCameraMove={onCameraMove}
                        meshFloors={meshFloorsChecked}
                        activeObjectId={activeObjectId}
                        hoverObjectId={hoverObjectId}
                        // @ts-ignore
                        currKioskObj={currKioskObj}
                        currKioskFloorIndex={currKioskFloorIndex}
                        routeTargetId={activeObjectId}
                        routeTargetFromId={activeFromObjectId}
                        currentFloorIndex={currentFloorIndex}
                        onPointerEnter={onPointerEnter}
                        onPointerLeave={onPointerLeave}
                        onPointerMove={onPointerMove}
                        onClick={onClick}
                        handleChangeFloor={handleFloorChange}
                        amenityTargetType={amenityTargetType}
                        escalatorNodes={meshFloors.escalator_nodes}
                        zoom={zoom}
                        handleCameraLength={handleCameraLength}
                        config={config}
                        ref={floorsMapRef}
                        cameraPos={cameraPos}
                        controlPos={controlPos}
                        handleCameraAndControlsChange={handleCameraAndControlsChange}
                    />
                </Canvas>
                <div ref={labelRef}
                    style={{
                        position: 'absolute',
                        padding: 10,
                        background: '#ffffff',
                        fontWeight: 600,
                        color: '#000000',
                        fontSize: 16,
                        display: currentHoveredObject ? 'block' : 'none',
                        top: 300,
                        left: 400,
                        opacity: .8,
                    }}
                >
                    <div>
                        {currentHoveredObject && currentHoveredObject.userData.storeName}
                    </div>
                </div>

            </div>

            <RetailerInfoModal
                amenity={selectedAmenityType}
                show={showRetailerInfo}
                onHide={() => setShowRetailerInfo(false)}
                centerId={params.config.CENTER_ID ? params.config.CENTER_ID.toString() : ''}
                mapObjectName={selectedActiveObjectId}
                retailers={data?.retailers}
                mapObjs={data?.map_objs}
                webApiURI={params.webApiURI ?? ''}
                onClick={handleRetailerInfoOnClick}
                kioskId={config.KIOSK.toString()}
            />
        </>
    )
});



export default SceneComponent;
